<template>
    <div class="watch-list-form-container"
    @click="onClickEvent"
    :style="Style"
    >
        <div class="wl-form-header-text">
            <div class="wl-form-header-text">
                {{item.wl_name}}
            </div>
        </div>
        <div class="wl-form-numCode">
            {{item.codeLen}}
        </div>
    </div>
</template>

<script>
export default {
    props : ['item', 'selectedItem'],
    computed : {
        Style () {
            if (this.$props.selectedItem === this.$props.item.idx) {
                return {
                    'background-color' : "#21384d",
                    'border-left' : "3px solid #2962ff"
                }
            }
            return {}
        }
    },
    methods : {
        onClickEvent (e) {
            e.preventDefault()
            this.$emit("onEvent", {
                event : "selected",
                args : this.$props.item
            })
        },
    },
    data () {
        return {}
    }
}
</script>

<style scoped>
.watch-list-form-container {
    color: #d1d4dc;
    position: relative;
    padding: 15px 15px 10px;
    border-bottom: 1px solid #4f5966;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    white-space: nowrap;
}
.watch-list-form-container:hover {
    background-color : #2a2e39;
}
.wl-form-header-text{
    display: flex;
    flex: 0 1 100%;
    justify-content: space-between; 
    box-sizing: border-box;
    min-width: 0;
    max-width: 100%;
    padding-bottom: 5px;
    height: 15px;
    line-height: 15px;
    vertical-align: middle;
}
.wl-form-numCode{
    margin-right: 2px;
    position: relative;
    align-items: center;
    white-space: nowrap;
    text-align: left;
}
</style>