<template>
    <div class="watch-list-item"
    @mouseover="onMouseUp"
    @mouseleave="onMouseLeave"
    @click="onClickEvent"
    :style="selectedStyle"
    >
        <span class="watch-list-flag"></span>
        <span class="watch-list-name" :style="{
            color : cColor,
            'fontSize' : `${fontSize}` + 'px' 
        }">{{item.hname}}</span>
        
        <div class="watch-list-deleteButton" 
        v-if="mouseIsHover"
        style="color : #fff"
        @click="onDeleteItem"
        >
            <span class="deleteButton-text">
                <svg class="svg-watch-list-padding" xmlns="http://www.w3.org/2000/svg" width="18" height="18">
                    <path fill="currentColor" d="M9.707 9l4.647-4.646-.707-.708L9 8.293 4.354 3.646l-.708.708L8.293 9l-4.647 4.646.708.708L9 9.707l4.646 4.647.708-.707L9.707 9z">
                    </path>
                </svg>
            </span>
        </div>
    </div>
</template>

<script>
export default {
    name : "WatchListItem",
    props : {
        item : {
            type : Object,
            default : {
                hname : "",
                code : "000000"
            },
        }, 
        selectedCode : {
            type : String,
            default : "",
        },
        HoverColor : {
            type : String,
            default : "#2a2e39"
        },
        color : {
            type : String,
            default : "#fff"
        },
        IsHover : {
            type : Boolean,
            default : false,
        },
        fontSize : {
            type : Number,
            default : 12,
        }
    },
    methods : {
        onMouseUp(e) {
            if (this.$props.IsHover) {
                this.backColor = this.$props.HoverColor
                this.cColor = "rgb(240, 185, 11)"
                this.mouseIsHover = true
            }
        },
        onMouseLeave (e) {
            if (this.$props.IsHover) {
                this.backColor = null
                this.cColor = this.$props.color
                this.mouseIsHover = false
            }
        },
        onClickEvent (e) {
            e.preventDefault()
            if (this.IsDestory) return;

            this.$emit("onEvent", {
                event : "selected",
                args : this.$props.item
            })
        },
        onDeleteItem(e) {
            e.preventDefault()
            this.IsDestory = true
            this.$emit("onEvent", {
                event : "remove",
                args : this.$props.item
            })
        }
    },
    computed : {
        selectedStyle () {
            return {
                'background-color' : this.backColor,
                border : (this.$props.selectedCode === this.$props.item.code) ? "3px solid #2962ff" : "" 
            }
        }
    },
    data () {
        return {
            cColor : this.$props.color,
            backColor : null,
            mouseIsHover : false,
            IsDestory : false,
        }
    }
}
</script>

<style>
.watch-list-item{
    display: flex;
    flex: 1 1 auto;
    width: 97%;
}
.watch-list-flag{
    align-self: center;
    margin-right: 4px;
}
.watch-list-name{
    display: flex;
    min-width: 0;
    max-width: 100%;
    padding-top: 3px;
    padding-bottom: 3px;
    align-items: center;
    overflow: hidden;
    white-space: nowrap;
    justify-content: flex-start;
    padding-right: 3px;
    flex: 1 4 90px;
}
.watch-list-deleteButton{
    align-items: center;
    justify-content: center;
    width : 20px;
    left : -10px;
    padding: 0;
}
.svg-watch-list-padding{
    margin-top : 5px;
}
.svg-watch-list-padding:hover{
    color : #fff;
    background-color: #1e222d;
}
</style>