<template>
<div class="drag-tool-menu">
    <!--<div class="extended-hover-zone"></div>-->
    <div class="drag-tool-menu-items" :style="{
        width: width+'px',
        minWidth: width+'px',
        maxHeight: height+'px',
        overflow: height ? 'auto' : 'visible'
    }">
        <component v-for="(item, index) in menu"
        :is="get_component(item.is)"
        :item="item"
        :class="item.class"
        :key="'menu-'+index"
        :index="index"
        />
    </div>
</div>
</template>

<script>
import DragToolMenuItem from './DragToolMenuItem.vue'
import DragToolMenuSeparator from './DragToolMenuSeparator.vue'
import DragToolMenuColor from './DragToolMenuColor.vue'

export default {
    components: { DragToolMenuItem, DragToolMenuSeparator, DragToolMenuColor },
    props: {
        menu: {
            type: Array,
            required:true
        },
        width: Number,
        height: Number,
    },
    methods: {
        get_component(is) {
            if(typeof is == "object") return is;
            else if(typeof is == "string") return 'drag-tool-menu-'+is;
            return 'drag-tool-menu-item';
        }
    }
}
</script>

<style>
.extended-hover-zone {
    position: absolute;
    top: 0;
    left: -100px;
    right: -100px;
    bottom: -40px;
}
.bar-menu-items {
    position: relative;
    min-width: 160px;
    color: rgba(0, 0, 0, 0.7);
    background: white;
    padding: 5px 0;
    box-shadow: 0 2px 4px rgb(0 0 0 / 20%);/*rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;*/
}
</style>