<template>
    <div class="modal-input">
        <div class="inputContainer">
            <input type="text" class="input-3n5" autocomplete="off" data-role="search" placeholder="" 
            value="" ref="codeInput"
            @input="onInput"
            />
        <!-- ENTOR  @keyup.enter="$emit('onHandleInput', $refs.codeInput.value)"-->
        </div>
        <span class="modal-input-icon" @click="$emit('onHandleInput', $refs.codeInput.value)">
            <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" fill="none">
                <path stroke="currentColor" d="M12.4 12.5a7 7 0 1 0-4.9 2 7 7 0 0 0 4.9-2zm0 0l5.101 5"></path>
            </svg>
        </span>
    </div>
</template>


<script>
export default {
    name: "CodeSearchEngine",
    methods: {
        onInput (e) {
            const inputKeyWord = e.target.value
            if (inputKeyWord === ""){
                this.$emit("onHandleInput", {
                    IsInput : false,
                    output : {}
                })
                return;
            }
            let codeDicList = Object.keys(this.CodeDic)
            .filter(x => x.indexOf(inputKeyWord) != -1)
            let nameDicList = Object.keys(this.NameDic)
            .filter(x => x.indexOf(inputKeyWord) != -1)
            
            if (codeDicList.length > nameDicList.length) {
                return this.$emit("onHandleInput", {
                    IsInput : true,
                    output : codeDicList.map(
                        (x, i) => { return { 
                            code : this.CodeDic[x], 
                            hname : x 
                        }})
                })
            } else {
                return this.$emit("onHandleInput", {
                    IsInput : true,
                    output : nameDicList.map(
                        (x, i) => { return { 
                            hname : this.NameDic[x], 
                            code : x 
                        }})
                })
            }
        }
    },
    mounted() {
        this.CodeDic = this.$store.getters.getCodeDic
        this.NameDic = this.$store.getters.getNameDic
    },
    data () {
        return {
            NameDic : [],
            CodeDic : [],
        }
    }
}
</script>>

<style>
.modal-input{
    display: flex;
    position: relative;
    align-items: center;
    flex-shrink: 0;
    cursor: default;
    border-top: 1px solid #2a2e39;
    border-bottom: 1px solid #2a2e39;
}
.inputContainer {
    width: 100%;
    height: 24px;
    padding: 8px 16px 8px 47px;
}
.input-3n5{
    width: 100%;
    height: 100%;
    border: none;
    padding: 0;
    margin: 0;
    background-color: transparent;
    font-size: 16px;
    color : white;
}
.modal-input-icon {
    position: absolute;
    left: 20px;
    height: 18px;
    top: calc(50% - 9px);
    pointer-events: none;
    color: #a3a6af;
}
</style>