<template>
<div class="sub-contain-wrapper">
    <main-form :tools="tools">
    <div class="body-content-container" slot="body-content-slot">
        <perfect-scrollbar ref="SubCon-scroll">
            <draggable 
            v-model="groupData" 
            draggable="#dragging-id"
            v-bind="dragOptions"
            >
                <watch-list-item-alert v-for="(item, index) in groupData"
                :item="item"
                :key="`${item.code} + ${index}`"
                :IsHover="true"
                :fontSize=12
                :selectedCode="selectedCode"
                @onEvent="onWatchListEvent"
                id="dragging-id"
                />
            </draggable>

        </perfect-scrollbar>
    </div>
    </main-form>
</div>
</template>

<script>
import { ModalBus } from './js/eventBus.js'
import MainForm from './MainForm.vue';
import draggable from "vuedraggable";
import { PerfectScrollbar } from "vue2-perfect-scrollbar";
import "vue2-perfect-scrollbar/dist/vue2-perfect-scrollbar.css";
import WatchListItemAlert from './iterableForm/WatchListItemAlert.vue';
import identifier from './js/comAdapter'

export default {
    name: "SubContainer",
    components : {PerfectScrollbar, MainForm, WatchListItemAlert, draggable},
    mixins : [identifier],
    props: [],
    methods: {
        onWatchListEvent(d){
            switch (d.event) {
                case "remove":
                    this.groupData = this.groupData.filter(n => n.id !== d.args.id)
     
                    break;
                case "selected":
                    this.selectedCode = d.args.code                   
                    this.loadChart(this.selectedCode)

                    break;
            }
        },
        render () {
            let d = this.$store.getters.getRequestInfo
            switch(d.event) {
                case "init-page":
                    this.nowCode = this.$store.getters.getCode
                    var gridDic = this.$store.getters.getOnChartGrid

                    ///////////////////////////////////////
                    var dic_obj = gridDic[this.nowCode]

                    if (!Object.keys(gridDic).includes(this.nowCode)){
                        this.groupData ={}
                        return
                    }

                    this.groupData = Object.values(dic_obj).map(
                        (x, i)=> {return  { 
                        code : x.settings.p1[4],
                        hname : x.settings.p1[3],
                        id : x.settings.$uuid,
                        price : x.settings.p1[1],}
                        }
                    )
                    this.selectedCode = this.nowCode

                    break

                case 'load-chart':                
                    this.nowCode = this.$store.getters.getCode
                    var gridDic = this.$store.getters.getOnChartGrid

                    if (!Object.keys(gridDic).includes(this.nowCode)){
                        this.groupData ={}
                        return
                    }

                    var dic_obj = gridDic[this.nowCode]

                    this.groupData = Object.values(dic_obj).map(
                        (x, i)=> {return  { 
                        code : x.settings.p1[4],
                        hname : x.settings.p1[3],
                        id : x.settings.$uuid,
                        price : x.settings.p1[1],}
                        }
                    )
                    this.selectedCode = this.nowCode

                    this.$store.commit('render', {
                        id : this._uid,
                        event : 'grid-change'
                    })
                    break;
                
                case "grid-change":
                    this.nowCode = this.$store.getters.getCode
                    var gridDic = this.$store.getters.getOnChartGrid

                    ///////////////////////////////////////
                    var dic_obj = gridDic[this.nowCode]

                    if (!Object.keys(gridDic).includes(this.nowCode)){
                        this.groupData ={}
                        return 
                    }

                    this.groupData = Object.values(dic_obj).map(
                        (x, i)=> {return  { 
                        code : x.settings.p1[4],
                        hname : x.settings.p1[3],
                        id : x.settings.$uuid,
                        price : x.settings.p1[1],}
                        }
                    )
                    this.selectedCode = this.code

                    break;
                
            }
        },
    },
    mounted() {},
    beforeDestroy() {},
    computed: {
    },
    data() {
        return {
            tools : [
                {text : ''},
                {is : 'separator'},
                {text : "text",
                    click: ()=> {
                        ModalBus.$emit('open-list', {
                            modal_case : 'modal-setting-list',
                            data : this.$store.getters.getOnChartGrid,
                        })
                    }
                },
                {is : 'separator'},
                {is : 'spacer'},
                {is : 'separator'},
                {text : 'x'},
            ],
            groupData :  [],
            selectedCode : "",
            nowCode : "",
            dragOptions : {
                animation: 200,
                group: "description",
                disabled: false,
                ghostClass: "ghost"
            },
        }
    },


    watch: {
        groupData : function(n, p) {

        },
    },     
}
</script>

<style>
.sub-contain-wrapper{
    height: 100%;
}
.body-content-container{
    position: relative;
    cursor: default;
    overflow: hidden;
    height: 100%;
}
.scrollable-wrapper{
    position: relative;
    cursor: default;
    overflow: hidden;
    height: 100%;
}
</style>