<template>
    <div class="modal-body-alert-list-wrap">
        <div class="modal-body-header">
            <component 
            :is="component"
            />
        </div>
        <div class="modal-body-body">
            <perfect-scrollbar ref="modal-scroll">
                <component v-for="(item, index) in this.$store.getters.getOnChartGridList"
                :is="component"
                :item="item"
                :key="`${item.code} + ${index}`"
                :index="index"
                :nameColor="nameColor"
                :fontSize="fontSize"
                :IsHover="true"
                @onClickEvent="onClickEvent"
                />
            </perfect-scrollbar>
        </div>
    </div>
</template>

<script>
import { PerfectScrollbar } from "vue2-perfect-scrollbar";
import "vue2-perfect-scrollbar/dist/vue2-perfect-scrollbar.css";
import identifier from '../../js/comAdapter.js'

export default {
    name : "ModalBodyAlertList",
    props : ['component'],
    mixins : [identifier],
    components : { PerfectScrollbar},
    methods : {
        onClickEvent(d) {
            switch (d.event) {
                case "selected":
                this.selectedCode = d.code                   
                this.loadChart(this.selectedCode)

                break;
            }
        }
    },
    computed : {
    },
    data () {
        return {
            nameColor : '#fff',
            fontSize : 16,
        }
    },
    watch : {

    }
}
</script>

<style>

.modal-body-alert-list-wrap{
    position: relative;
    cursor: default;
    overflow:hidden;
}

.modal-body-body{
    height: 90%;
}
.ps{
    height : 100%;
}
.ps .ps__rail-y.ps--clicking {
  background-color: transparent;
  opacity: 0.3;
}
.ps .ps__rail-x:hover, .ps .ps__rail-y:hover, .ps .ps__rail-x:focus, .ps .ps__rail-y:focus, .ps .ps__rail-x.ps--clicking, .ps .ps__rail-y.ps--clicking {
    background-color: transparent;
    opacity: 0.9;
}
.ps__rail-y {
    width: 3px;
}
</style>