<template>
    <div class="drag-tool-button" :class="button_class" v-on="$listeners"
    @mousedown="(e) => e.preventDefault()"
    @click="(e) => (item.click && !item.disabled) ? item.click(e) : e.stopPropagation()"
    >
    <span v-if="item.icon" class="material-icons icon">{{ item.icon }}</span>
    <span v-if="item.html" class="label" v-html="item.html"></span>
    <span v-else-if="item.text" class="label">{{ item.text }}</span>
    
    <component class="menu" v-if="item.menu"
        :is="get_component(item.menu)"
        :menu="item.menu"
        :class="item.menu_class"
        :id="item.menu_id"
        :width="item.menu_width"
        :height="item.menu_height"
         />
    </div>
</template>

<script>
import hotkey_manager from './imports/hotkey-manager.js'
import DragToolMenu from './DragToolMenu.vue'

export default {
    name : "DragToolBarButtonGen",
    components: {
        DragToolMenu
    },

    mixins: [hotkey_manager],
    props: {
        item: {
        type: Object,
        required: true
        },
        is_open: Boolean,
        selected: Boolean,
    },
    methods: {
        get_component (is) {
            if(is && !Array.isArray(is) && typeof is == "object") return is;
            else return "drag-tool-menu";
        },
    },
    computed: {
        is_menu () { return this.item.menu ? true : false; },
        button_class() {
            const open = this.is_open && this.is_menu && this.$props.selected;
            const active = this.item.active;
            const disabled = this.item.disabled;
            return {open, active, disabled}
        },
    },
    mounted() {},
    beforeDestroy() {},
    data() {
        return {};
    },
}
</script>

<style>
.drag-tool-button {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    /*padding : 7px;*/
    height: 56px;
    min-width: 56px;
    border-radius: 3px;
    white-space: nowrap;
}


.drag-tool-button > .menu {
    position: absolute;
    left: 0;
    top: 100%;
    z-index: 1006;
    display: None;
}

.drag-tool-button.open > .menu {
    position: absolute;
    left: 0;
    top: 100%;
    display: block;
    z-index: 1006;
    background-color: #fff;
}

.drag-tool-button:hover {
    background-color: #76878319;
}
</style>