<template>
    <div class="top-tool-wrapper">
        <top-tool-bar :content="create_top_tool_content">
        </top-tool-bar>
    </div>
</template>

<script>
import { ModalBus } from '../js/eventBus.js'
import TopToolBar from './TopToolBar.vue'

export default {
    name: "TopToolWrapper",
    components: {
        TopToolBar
    },
    props: ['tf', 'code', 'hname'],
    methods: {
        init_code_input(){
            this.code_text = this.$props.code
            return { 
                text : this.code_text,
                click: ()=>{
                    ModalBus.$emit('search-add-modal', { 
                        modal_case: "modal-search", 
                        self: (x) => { 
                            this.$emit("top-tool-event", {
                                event : "upload-code",
                                args  : x
                            })}
                    });
                }
            }
        },
        init_time_menu() { 
            let ret = []
            let timeframe = this.$store.getters.getTimeFrame
            let tf_list = timeframe.map( tf => {
                return { 
                    text: tf,
                    click: () => {
                        this.$emit("top-tool-event", {
                            event : "change-tf",
                            args  : tf
                        });
                    }
                };
            });
            this.time_text = this.$props.tf;

            for (var i = 0; i < tf_list.length - 1; i++){
                ret.push(tf_list[i]);
                ret.push({is : 'separator'});
            }
            if (ret.length === 0){
                return { text : this.time_text };
            }
            ret.push(tf_list[tf_list.length - 1]);
            return {
                text : this.time_text,
                menu : ret
            };
        },
        test(){
            this.$emit("upload")
        }
    },
    computed: {
        create_top_tool_content() {

            return [
                this.init_code_input(),
                { is: "separator" },
                this.init_time_menu(),
                { is: "separator" },
                {
                    text: "a",
                    menu: [
                        {text : "a"},
                        {is : 'separator'},
                        {text : "b"},
                        {is : 'separator'},
                        {text : "c"},
                        {is : 'separator'},
                        {text : "d"},
                        {is : 'separator'},
                        {text : "e"},
                    ]
                },
                { is: "separator" },
                { is: "spacer" },
                { is: "separator" },
                {text : "X"}
            ]
        },
    },
    mounted() {

    },
    data() {
        return { 
            time_text : "",
            code_text : "      ",
        }
    },
}
</script>

