import Vue from "vue";
import Vuex, { Store } from "vuex";
import axios from "axios";

Vue.use(Vuex);

const defaultURL = "http://127.0.0.1:5000/";
const numCache = 15;

export default new Vuex.Store({
    state: {
        code    : "",
        hname   : "",
        tf      : "D",
        WATCH_LIST : [],
        wl_index : -1,

        dtoolbar_state: [],
        dtoolbar: false,

        colors  : "",

        charts  : [],
        CODE_DIC   : [],
        NAME_DIC   : [],
        TIME_FRAME : ['1Min', '3Min', '5Min', '10Min', '15Min', '30Min', '45Min', 'D'],
        
        ON_CHART_GRID : {},
        ON_CHART_GRID_lIST : [],

        mComponent : [], // register components
        render : 0,      // when need render, just add 1
        requestInfo : {},

    },
    actions: {
        sendInfo: ({state}, args) => {
            axios({
                method : 'post',
                contentType: 'application/json',
                url : `${defaultURL}user`,
                dataType : 'json',
                data : JSON.stringify({ args : {
                    code : state.code,
                    tf : state.tf,
                    WATCH_LIST : state.WATCH_LIST,
                    wl_index : state.wl_index
                }}),
            }).then((res) => {
            }).catch((error)=>{
                console.log(error);
            })
        },
        initCodeNameDic: ({commit}) => {
            return new Promise((resolve, reject) => {
                axios.get( `${defaultURL}`, {
                    params : {init_cond : true}
                }).then((res)=>{
                    commit("upDateCodeNameDic", res.data)
                    commit("registerChart", {
                        code : res.data.code,
                        chart : res.data.chart,
                        requestInfo : -1
                    })
                    commit("updateTF", res.data.tf)
                    commit("updateCodeName", res.data.code)
                    commit("updateWL", {
                        idx : res.data.wl_index,
                        wl_list : res.data.WATCH_LIST
                    })
                    commit("render", {
                        id : -1,
                        event : "init-page"
                    })
                    resolve()
                }).catch((error)=>{
                    // @TODO error handling 
                    console.log(error);
                    reject();
                })
            })
        },
        getCharts: ({commit}, args) => {
            return new Promise((resolve, reject) => {
                axios.get( `${defaultURL}`, {
                    params : {code : args.code}
                }).then((res)=>{
                    commit("registerChart", {
                        chart : res.data,
                        code : args.code,
                        id : args.id
                    })
                    let event = args.lock ? 'lock-chart' : 'load-chart' 
                    if (!args.lock){
                        commit('updateCodeName', args.code)                 
                    }
                    commit('render', {
                        id : args.id,
                        event : event
                    })
                    resolve()
                }).catch((error)=>{
                    // @TODO error handling 
                    console.log(error);
                    reject();
                })
            });
        }
    },
    getters: {
        getDtoolbar: (state) =>{
            return state.dtoolbar
        },
        getColors: (state) => {
            return state.colors
        },

        getCode: (state) => {
            return state.code
        },
        getHname: (state) => {
            return state.hname
        },
        getTF: (state) => {
            return state.tf
        },
        getGridId: (state) => {
            return state.grid_id 
        },
        getColors: (state) => {
            return state.colors
        },
        getCodeDic: (state) => {
            return state.CODE_DIC
        },
        getNameDic: (state) => {
            return state.NAME_DIC
        },
        getTimeFrame: (state) => {
            return state.TIME_FRAME
        },
        getCodeNameDIC: (state) => (id) => {
            if (id in state.NAME_DIC) {
                return {
                    code  : id,
                    hname : state.NAME_DIC[id]
                }
            }
            if (id in state.CODE_DIC) {
                return {
                    code : state.CODE_DIC[id],
                    hname : id
                }
            }
            return {}
        },
        getChartList: (state) => {
            return state.charts
        },
        getChart: (state) => {
            if (state.charts.length === 0) return null
            return state.charts[state.charts.length - 1]
        },
        getRequestInfo : (state) => {
            return state.requestInfo
        },
        getWatchList : (state) => {
            return state.WATCH_LIST
        },
        getWatchListItem : (state) => {
            return state.WATCH_LIST[state.wl_index]
        },
        getWatchListIndex : (state) => {
            return state.wl_index
        },

        getOnChartGrid : (state) => {
            return state.ON_CHART_GRID
        },

        
        getOnChartGridList : (state) => {
            return state.ON_CHART_GRID_lIST
        },

        dispatchReceived: (state) => {
            return state.ON_CHART_GRID
        },

    },
    mutations: {
        render : (state, comId) => {
            state.requestInfo = comId
            // Watch state
            state.render = ( state.render + 1 ) % 100
        },
        updateCharts : (state, idx) => {
            state.charts.push(state.charts.splice(idx, 1)[0])
        },
        upDateCodeNameDic: (state, overview, send=true) => {
            state.CODE_DIC = overview["CODE_DIC"]
            state.NAME_DIC = overview['NAME_DIC']
        },
        updateTF: (state, overview) => {
            state.tf = overview
        },
        updateWL: (state, d) => {
            state.WATCH_LIST = d.wl_list
            state.wl_index = d.idx
        },
        updateWatchListItem: (state, codelist) => {
            state.WATCH_LIST[state.wl_index].codelist = codelist
            state.WATCH_LIST[state.wl_index].codeLen  = codelist.length
        },
        updateCodeName: (state, code) => {
            state.code  = code
            if (code in state.NAME_DIC) {
                state.hname = state.NAME_DIC[code]
            } else {
                state.hname = ""    
            } 
        },
        registerChart: (state, d) => {
            if (state.charts.length === numCache){ 
                // remove first element
                state.charts.shift() 
            }
            state.charts.push({
                code : d.code,
                chart : d.chart,
                reqestInfo : d.id,
            })
        },
        registerComponent : (state, id) => {
            state.mComponent.push(id)
        },

        updateDtoolBar: (state, overview) => {
            state.dtoolbar = overview;
        },
        updateColor: (state, overview) => {
            state.colors = overview;
        },
        //update onchart grid setting by dictonry for code
        updateOnChartGrid: (state, overview) => {
            var code_1 = overview[0]
            var onchart = overview[1]

            if (state.ON_CHART_GRID[code_1]){
                state.ON_CHART_GRID[code_1] = onchart;
                //get alert code list 
                var hname_1 = state.NAME_DIC[code_1]
                var input_value = {code : code_1, hname : hname_1}
                var index = state.ON_CHART_GRID_lIST.findIndex(x => x.hname === hname_1)

                if (Object.values(state.ON_CHART_GRID[code_1]).length > 0){
                    if (index == -1){
                        state.ON_CHART_GRID_lIST.push(input_value)
                    }
                }
                else if (Object.values(state.ON_CHART_GRID[code_1]).length < 1){
                    if (index != -1){         
                        state.ON_CHART_GRID_lIST.splice(index, 1);
                    }
                }
                //
            }
            else{
                state.ON_CHART_GRID[code_1] = [];
            }

        },
    },
    modules: {}
})