<template>
<div class="sub-contain-wrapper">
    <main-form :tools="tools">
    <div class="body-content-container" slot="body-content-slot">
        <perfect-scrollbar ref="SubCon-scroll">
            <draggable 
            v-model="groupData" 
            draggable="#dragging-id"
            v-bind="dragOptions"
            >
                <watch-list-item v-for="(item, index) in groupData"
                :item="item"
                :key="`${item.code} + ${index}`"
                :IsHover="true"
                :fontSize=12
                :selectedCode="selectedCode"
                @onEvent="onWatchListEvent"
                id="dragging-id"
                />
            </draggable>
        </perfect-scrollbar>
    </div>
    </main-form>
    <input type="file" ref="file_input" @change='onFileUpload' style="display:none"/>
    <!--
    <json-csv-saver
        :data="jsonData"
        :name="dataFile"
        :labels="labels"
        :fields="fields"
    >
        <button class="button">Download</button>
    </json-csv-saver>
    -->
</div>
</template>

<script>
import { ModalBus } from './js/eventBus.js'
import MainForm from './MainForm.vue';
import draggable from "vuedraggable";
import { PerfectScrollbar } from "vue2-perfect-scrollbar";
import "vue2-perfect-scrollbar/dist/vue2-perfect-scrollbar.css";
import WatchListItem from './iterableForm/WatchListItem.vue';
import comAdapter from './js/comAdapter'


const svg_width = 18
const svg_hight = 18

export default {
    name: "MainWatchList",
    components : {PerfectScrollbar, MainForm, WatchListItem, draggable},
    mixins : [comAdapter],
    props: [],
    methods: {
        onFileUpload(e) {
            // @TODO  csv file upload process
            // XMLHttpRequest   requests
            console.log("FILE LOAD")
        },
        onWatchListEvent(d){
            switch (d.event) {
                case "remove":
                    this.groupData = this.groupData.filter(n => n.code !== d.args.code)
                    break;
                case "selected":
                    this.selectedCode = d.args.code
                    this.loadChart(this.selectedCode)
                    break;
                case "add":
                    if (d.args.index === -1){
                        this.groupData.push({ code : d.args.code, hname : d.args.hname})
                        break;
                    }
                    this.groupData.splice(d.args.index, 0, {code : d.args.code, hname : d.args.hname})
                    break;
            }
        },
        init_content() {
            return [
                { text : this.groupName,
                    click: () => {
                        ModalBus.$emit('watch-modal', {
                            modal_case : 'new-list',
                            comId : this._uid
                        })
                    }
                },
                { is : 'separator'},
                { is: "spacer" },
                { is : 'separator'},
                { html : `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 28 28" width="${svg_width}" height="${svg_hight}"><path fill="currentColor" d="M13.5 6a8.5 8.5 0 1 0 0 17 8.5 8.5 0 0 0 0-17zM4 14.5a9.5 9.5 0 1 1 19 0 9.5 9.5 0 0 1-19 0z"></path><path fill="currentColor" d="M9 14h4v-4h1v4h4v1h-4v4h-1v-4H9v-1z"></path></svg>`,
                    click: ()=> {
                        ModalBus.$emit('search-add-modal', {
                            modal_case : 'modal-add',
                            data : this.groupData,
                            self : {
                                'getData' : () => { return this.groupData},
                                'onEvent' : (x) => {
                                this.onWatchListEvent(x)
                            }
                            }
                        })
                    }
                },
                { is : 'separator'},
                {   html : `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 28 28" width="${svg_width}" height="${svg_hight}"><g fill="currentColor" fill-rule="evenodd"><path fill-rule="nonzero" d="M14 17a3 3 0 1 1 0-6 3 3 0 0 1 0 6zm0-1a2 2 0 1 0 0-4 2 2 0 0 0 0 4z"></path><path d="M5.005 16A1.003 1.003 0 0 1 4 14.992v-1.984A.998.998 0 0 1 5 12h1.252a7.87 7.87 0 0 1 .853-2.06l-.919-.925c-.356-.397-.348-1 .03-1.379l1.42-1.42a1 1 0 0 1 1.416.007l.889.882A7.96 7.96 0 0 1 12 6.253V5c0-.514.46-1 1-1h2c.557 0 1 .44 1 1v1.253a7.96 7.96 0 0 1 2.06.852l.888-.882a1 1 0 0 1 1.416-.006l1.42 1.42a.999.999 0 0 1 .029 1.377s-.4.406-.918.926a7.87 7.87 0 0 1 .853 2.06H23c.557 0 1 .447 1 1.008v1.984A.998.998 0 0 1 23 16h-1.252a7.87 7.87 0 0 1-.853 2.06l.882.888a1 1 0 0 1 .006 1.416l-1.42 1.42a1 1 0 0 1-1.415-.007l-.889-.882a7.96 7.96 0 0 1-2.059.852v1.248c0 .56-.45 1.005-1.008 1.005h-1.984A1.004 1.004 0 0 1 12 22.995v-1.248a7.96 7.96 0 0 1-2.06-.852l-.888.882a1 1 0 0 1-1.416.006l-1.42-1.42a1 1 0 0 1 .007-1.415l.882-.888A7.87 7.87 0 0 1 6.252 16H5.005zm3.378-6.193l-.227.34A6.884 6.884 0 0 0 7.14 12.6l-.082.4H5.005C5.002 13 5 13.664 5 14.992c0 .005.686.008 2.058.008l.082.4c.18.883.52 1.71 1.016 2.453l.227.34-1.45 1.46c-.004.003.466.477 1.41 1.422l1.464-1.458.34.227a6.959 6.959 0 0 0 2.454 1.016l.399.083v2.052c0 .003.664.005 1.992.005.005 0 .008-.686.008-2.057l.399-.083a6.959 6.959 0 0 0 2.454-1.016l.34-.227 1.46 1.45c.003.004.477-.466 1.422-1.41l-1.458-1.464.227-.34A6.884 6.884 0 0 0 20.86 15.4l.082-.4h2.053c.003 0 .005-.664.005-1.992 0-.005-.686-.008-2.058-.008l-.082-.4a6.884 6.884 0 0 0-1.016-2.453l-.227-.34 1.376-1.384.081-.082-1.416-1.416-1.465 1.458-.34-.227a6.959 6.959 0 0 0-2.454-1.016L15 7.057V5c0-.003-.664-.003-1.992 0-.005 0-.008.686-.008 2.057l-.399.083a6.959 6.959 0 0 0-2.454 1.016l-.34.227-1.46-1.45c-.003-.004-.477.466-1.421 1.408l1.457 1.466z"></path></g></svg>`,
                    menu : [
                    {
                        text : "",
                        click: () => {
                            console.log("")
                        }  
                    },
                    {is : 'separator'},
                    { text : "",
                        click: () => {
                            this.$refs.file_input.click()
                        }
                    },
                    {is : 'separator'},
                    { text : "",
                        click: () => {
                            // @TODO draggable      
                            console.log("save")
                        }
                    },
                    {is : 'separator'},
                    {
                        text : "",
                        click: () => {
                            // @TODO  List 
                            console.log("")
                        }
                    }],
                    'menu_left' : -100
                },
                { is : 'separator'},
                {html : `<svg xmlns="http://www.w3.org/2000/svg" width="${svg_width}" height="${svg_hight}">
                    <path fill="currentColor" d="M9.707 9l4.647-4.646-.707-.708L9 8.293 4.354 3.646l-.708.708L8.293 9l-4.647 4.646.708.708L9 9.707l4.646 4.647.708-.707L9.707 9z">
                    </path>
                </svg>`}
            ]
        },
        render () {
            let d = this.$store.getters.getRequestInfo
            switch(d.event) {
                case "init-page":
                case "load-watch-list":
                    let wl_list = this.$store.getters.getWatchListItem
                    const n_obj = this.$store.getters.getNameDic
                    this.groupData = wl_list.codelist.map(
                        (x, i)=> {return { code : x, hname : n_obj[x]}}
                    )
                    this.selectedCode = this.$store.getters.getCode
                    this.groupName = wl_list.wl_name
                    this.tools = this.init_content()
                    break;
                case 'load-chart':
                    this.selectedCode = this.$store.getters.getCode
                    break;
            }
        },
    },
    computed: {
    },
    mounted() {},
    beforeDestroy() {},
    data() {
        return {
            tools : this.init_content(),
            groupName : "",
            groupData : [],
            selectedCode : "",
            dragOptions : {
                animation: 200,
                group: "description",
                disabled: false,
                ghostClass: "ghost"
            },
        }
    },
    watch : {
        groupData : function(n, p) {
            this.$store.commit("updateWatchListItem", n.map(x=>x.code))
        }
    }
}
</script>

<style>
.sub-contain-wrapper{
    height: 100%;
}
.body-content-container{
    position: relative;
    cursor: default;
    overflow: hidden;
    height: 100%;
}
.scrollable-wrapper{
    position: relative;
    cursor: default;
    overflow: hidden;
    height: 100%;
}
</style>