<template>
    <div class="watch-modal-backdrop"
        v-if="modal_open"
        :class="{open : modal_open}"
        >
        <div class="watch-list-modal">
            <div class="wl-modal-header">
                <span class='wl-modal-tab1-span'>
                     
                </span>
                <div class="w1-modal-header-button-tab">
                    <div class="modal-close" @click="handleModalClose(false)">
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 17 17" width="17" height="17" fill="none">
                            <path stroke="currentColor" stroke-width="1.2" d="M1 1l15 15m0-15L1 16"></path>
                        </svg>
                    </div>
                </div>
            </div>
            <div class="wl-modal-body">
                <div class="wl-modal-body-list">
                    <div class="wl-modal-body-list-header">
                        <div class="search-input-block">
                            <svg class="search-header-svg" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1024 973.125">
                                <path d="M960 832L710.875 582.875C746.438 524.812 768 457.155 768 384 768 171.97 596 0 384 0 171.97 0 0 171.97 0 384c0 212 171.97 384 384 384 73.156 0 140.812-21.562 198.875-57L832 960c17.5 17.5 46.5 17.375 64 0l64-64c17.5-17.5 17.5-46.5 0-64zM384 640c-141.375 0-256-114.625-256-256s114.625-256 256-256 256 114.625 256 256-114.625 256-256 256z"></path>
                            </svg>
                            <input type="text" class="search-input" placeholder="-" maxlength="1000" autocomplete="off">
                        </div>
                        <div class="new-note-btn"
                        @click="onWatchListClickEvent({event : 'add'})"
                        >
                            <svg class="new-note-svg" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 28 28">
                                <path fill="#ffffff" d="M18 10V0h-8v10H0v8h10v10h8V18h10v-8">
                                </path>
                            </svg>
                            
                        </div>
                    </div>
                    <div class="w1-modal-body-list-wrapper">
                        <perfect-scrollbar ref="SubCon-scroll">
                            <draggable 
                            v-model="groupData" 
                            draggable="#dragging-id"
                            v-bind="dragOptions"
                            @end="onDragEnd"
                            >
                                <watch-list-group v-for="(item, index) in groupData"
                                :item="item"
                                :key="`${index}`"
                                id="dragging-id"
                                :selectedItem="selectedItem"
                                @onEvent="onWatchListClickEvent"
                                />
                            </draggable>
                        </perfect-scrollbar>
                    </div>
                </div>
                <div class="wl-model-desc-block"
                v-if="IsSelected"
                >
                    <div class="wl-modal-desc-inner">
                        <div class="wl-remove-button"
                        @click="deleteDialog"
                        >
                            <span class="wl-remove-button-text"></span>
                        </div>
                    </div>
                    <div class="wl-modal-title-wrapper">
                        <input type="text" maxlength="100" class="wl-modal-title-input"
                        v-model="itemName"
                        placeholder=" " 
                        @input="onItemName"
                        ref="inputWatchListName"
                        >
                    </div>
                    <div class="wl-modal-move-container">
                        <div class="w1-modal-move-wrapper">
                            <div class="w1-modal-move-remove-wrapper">
                                <perfect-scrollbar ref="SubCon-scroll-outlist">
                                    <group-code-list v-for="(item, index) in outWLData"
                                    :key="`'Out_' + ${item.code} + '_' + ${index}`"
                                    :item="item"
                                    :InGroup="false"
                                    @onClickEvent="onWatchListClickEvent"
                                    />
                                </perfect-scrollbar>
                            </div>
                            <div class="wl-modal-move-buttons-wrapper">
                                <div class='wl-modal-move-to-letf'>
                                    <div class="wl-modal-left-svg">
                                    <svg class="w1-left-svg-button" 
                                    @click="onWatchListClickEvent({event : 'add-all-outlist'})"
                                    xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 10" width="60" height="30">
                                    <path fill="none" stroke="currentColor" stroke-width="1.5" d="M2 1l8 8 8-8"></path>
                                    </svg>
                                    </div>                                
                                </div>
                                <div class='wl-modal-move-to-right'>
                                    <div class="wl-modal-right-svg">
                                    <svg class="w1-right-svg-button" 
                                    @click="onWatchListClickEvent({event : 'remove-all-inlist'})"
                                    xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 10" width="60" height="30">
                                    <path  fill="none" stroke="currentColor" stroke-width="1.5" d="M2 1l8 8 8-8">
                                    </path>
                                    </svg>
                                    </div>
                                </div>
                            </div>
                            <div class="w1-modal-move-add-wrapper">
                                <perfect-scrollbar ref="SubCon-scroll-inlist">
                                    <group-code-list v-for="(item, index) in InWLData"
                                    :key="`'In_' + ${item.code} + '_' + ${index}`"
                                    :item="item"
                                    :InGroup="true"
                                    @onClickEvent="onWatchListClickEvent"
                                    />
                                </perfect-scrollbar>
                            </div>
                        </div>
                        <div class="wl-modal-footer-wrapper">
                                <div class="w1-footer-left-container">
                                    <div class="w1-footer-left-buttons">
                                        <div class="w1-footer-export"
                                        @click="onImportfile"
                                        >
                                            
                                        </div>
                                        <div class="w1-footer-import"
                                        @click='onExportfile'
                                        >
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 14" width="16" height="14" class="tv-screener-toolbar__button-icon tv-screener-toolbar__button-icon--export">
                                            <g fill="none" stroke="currentColor" stroke-width="1.5"><path d="M15 9v4H1V9M8 9V0"></path><path d="M5 6l3 3 3-3"></path></g></svg>
                                        </div>
                                    </div>
                                    <div class="w1-footer-register"
                                    @click="handleModalClose(true)"
                                    >
                                        
                                    </div>
                                </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <input type="file" ref="file_input" @change='onFileUpload' style="display:none"/>
    </div>
</template>

<script>
import { ModalBus } from '../js/eventBus.js'
import { PerfectScrollbar } from "vue2-perfect-scrollbar";
import "vue2-perfect-scrollbar/dist/vue2-perfect-scrollbar.css";
import WatchListGroup from './component/WatchListGroup.vue'
import draggable from "vuedraggable";
import fileManager from "./mixins/fileManager.js"
import GroupCodeList from "./component/GroupCodeList.vue"

export default {
    components : { PerfectScrollbar, WatchListGroup, draggable, GroupCodeList},
    mixins : [fileManager],
    data () {
        return {
            dragOptions : {
                animation: 200,
                group: "description",
                disabled: false,
                ghostClass: "ghost"
            },
            modal_open : false,
            component : null,
            groupData : [],
            selectedItem : -1,
            idx_render : -1,
            itemName : "",
            realIdx: -1,
            outWLData : [],
            InWLData : [],
            comId : -1,
        }
    },
    created () {
        ModalBus.$on('watch-modal', ({modal_case, comId}) => {
            const n_obj = this.$store.getters.getCodeDic
            this.modal_open = true
            this.groupData = this.$store.getters.getWatchList
            this.realIdx = this.$store.getters.getWatchListIndex
            for(let i=0; i < this.groupData.length ; i++){
                this.groupData[i].idx = i
            }
            this.idx_render = this.groupData.length - 1
            this.InWLData = this.groupData[0].codelist.map(
                (x, i)=> {return { code : x, hname : n_obj[x], idx : i} }
            )
            this.comId = comId
        })
    },
    beforeDestroy() {
        this.modal_open=false
    },
    methods: {
        handleModalClose(okay=false) {
            if (!!okay){
                //   upload
                this.$store.commit("updateWL", {
                    wl_list : this.groupData,
                    idx : this.realIdx
                })
                this.$store.commit('render', {
                    id : this.comId,
                    event : 'load-watch-list'
                })
            }
            this.modal_open = false
        },
        onImportfile() {
            this.$refs.file_input.click()
        },
        onExportfile (){
            let _str = ""
            for (let i = 0; i < this.InWLData.length; i++){
                _str += `${this.InWLData[i].hname},${this.InWLData[i].code}\n`
            }
            this.fileSaver(_str, '.txt', 'text/plain')
        },
        deleteDialog() {
            if (this.realIdx === -1) { return; }
            ModalBus.$emit('small-modal', {
                modal_case : "delete-item",
                deleteItemName : this.itemName,
                title : " ",
                self : this.onWatchListClickEvent,
            })
        },
        onFileUpload(e) {
            let file = e.target.files[0]
            const that = this
            this.reader.onload = function(progreeEvent){
                that.onWatchListClickEvent({
                    codelist : that.parseCSVFile (progreeEvent.target.result),
                    event : 'file-import'
                })
            }
            this.reader.readAsText(file, "euc-kr")   
        },
        onInitalize(){
            // Initialize
            this.selectedItem = -1
            this.itemName = ""
            this.realIdx = -1
            this.InWLData = []
            this.outWLData = []
        },
        _updateInOutDic(idx){
            const n_obj   = this.$store.getters.getNameDic
            this.InWLData = this.groupData[idx].codelist.map(x => {return {
                code : x, hname : n_obj[x] }})
            this.outWLData = []
        },
        onDragEnd(d){
            this.setByIndex(d.newIndex)
        },
        setByIndex(idx){
            this.selectedItem = this.groupData[idx].idx
            this.itemName = this.groupData[idx].wl_name
            this.realIdx = idx
            this._updateInOutDic(idx)
        },
        onWatchListClickEvent(d){
            switch (d.event) {
                case "selected":
                    this.selectedItem = d.args.idx
                    this.itemName = d.args.wl_name
                    this.realIdx = Object.keys(this.groupData).find(key => this.groupData[key].idx === d.args.idx)
                    this._updateInOutDic(this.realIdx)
                    break;
                case "add":
                    this.idx_render++
                    this.groupData.push({
                        wl_name : "",
                        codelist : [],
                        codeLen : 0,
                        idx : this.idx_render,
                    })
                    this.setByIndex(this.groupData.length - 1)
                    break;
                case "remove":
                    this.groupData = this.groupData.filter(n => n.idx !== this.selectedItem)
                    let groupDataLen = this.groupData.length
                    if (groupDataLen === 0) {
                        this.onInitalize()
                    } else if (this.realIdx === 0){
                        this.setByIndex(0)
                    } else {
                        this.setByIndex(this.realIdx - 1)
                    }
                    break;
                case 'remove-in-codelist':
                    this.InWLData = this.InWLData.filter(n => n.code !== d.code)
                    this.groupData[this.realIdx].codelist = this.InWLData.map(x=> x.code)
                    this.$set(this.groupData[this.realIdx], 'codeLen', this.InWLData.length)
                    if (this.outWLData.filter(n => n.code === d.code).length === 0) {
                        this.outWLData.push({
                            code : d.code,
                            hname : d.hname
                        })
                    }
                    break;
                case 'add-in-codelist':
                    this.outWLData = this.outWLData.filter(n => n.code !== d.code)
                    if (this.InWLData.filter(n => n.code === d.code).length === 0) {
                        this.InWLData.push({
                            code : d.code,
                            hname : d.hname
                        })
                        this.groupData[this.realIdx].codelist = this.InWLData.map(x=> x.code)
                        this.$set(this.groupData[this.realIdx], 'codeLen', this.InWLData.length)
                    }
                    break;
                case 'add-all-outlist':
                    this.outWLData = this.outWLData.filter(
                        n=> !this.InWLData.map(x=>x.code).includes(n.code)
                    )
                    if (this.outWLData !== 0){
                        this.InWLData = this.InWLData.concat(this.outWLData)
                        this.groupData[this.realIdx].codelist = this.InWLData.map(x=> x.code)
                        this.$set(this.groupData[this.realIdx], 'codeLen', this.InWLData.length)
                        this.outWLData = []
                    }
                    break;
                case 'remove-all-inlist':
                    this.InWLData = this.InWLData.filter(
                        n => !this.outWLData.map(x=>x.code).includes(n.code)
                    )
                    if (this.InWLData !== 0){
                        this.outWLData = this.outWLData.concat(this.InWLData)
                    }
                    this.InWLData = []
                    this.groupData[this.realIdx].codelist = []
                    this.$set(this.groupData[this.realIdx], 'codeLen', 0)
                    break;
                case 'file-import':
                    const n_obj = this.$store.getters.getNameDic
                    const codeDic = Object.keys(n_obj)
                    this.outWLData = d.codelist
                        .filter(n=> codeDic.includes(n))
                        .map(n=> {return { code : n, hname : n_obj[n] }})
                    break;
            }
        },
        onItemName(e){
            this.$set(this.groupData[this.realIdx], 'wl_name', this.itemName)
        },

    },
    computed: {
        IsSelected () {
            return this.realIdx !== -1
        }
    },
}
</script>

<style scoped>
