    text-overflow: ellipsis;
}
.wl-modal-move-container{
    padding-top : 20px;
    height : 87%;
    flex: 1 1 auto;
    display : flex;
    flex-direction: column;
}
.w1-modal-move-wrapper {
    display: flex;
