    background-color : #2a2e39;
}

.w1-modal-move-add-wrapper{
    flex-grow: 4.5;
    height: 100%;
    width: 100%;
    /* TODO */
    border: 1px solid #4f5966;
    align-content: center;
}
.wl-modal-footer-wrapper{
    width : 100%;
    height: 100%;
}
.w1-footer-left-container {
    padding: 40px 10px 12px;
    color: white;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    white-space: nowrap;
}
.w1-footer-left-buttons{
    display: flex;
