    background-color : #2a2e39;
}
.w1-footer-register{
    float: right;
    color: #787b86;
    background-color: #131722;
    border-color: #363a45;
    margin-right: 10px;
    position: relative;
    height: 32px;
    max-width: 250px;
    flex-shrink: 0;
    white-space: nowrap;
    padding: 8px 12px 0;
    box-sizing: border-box;
    border: 1px solid;
    border-radius: 2px;
    font-weight: 700;
    font-size: 12px;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
    vertical-align: middle;
    transition: background-color .175s ease,opacity .175s ease;
}
.w1-footer-register:hover{
    background-color : #2a2e39;
}


.search-input-block{
    position: relative;
    float: right;
    display: block;
}
.search-header-svg{
    fill: #50535e;
    position: absolute;
    top: 9px;
    left: 9px;
