    height: 15px;
    overflow: hidden;
}
.search-input{
    background-color: #1e222d;
    border: 1px solid #4f5966;
    color: #d1d4dc;
    font-size: 14px;
    padding: 0 5px;
    padding-left: 30px;
    height: 32px;
