
export default {
    methods : {
        search_handleInput(d) {
            if (d.IsInput){
                this.IsInInputMode = true
                // Why config.data undefined???????
                //    
                if (!!this.config.data){
                    this.config = {
                        data : d.output,
                        IsAddRemove : false
                    }
                }
                else {
                    this.$set(this.config, 'data', d.ouput)
                }
            } else {
                this.IsInInputMode = false
                this.$set(this.config, 'data', [])
            }
        },
        search_handleClick(d){
            this.self(d)
            this.handleModalClose()
        },
        add_handleInput(d){
            let data_ = this.updateFunc()
            if(d.IsInput){
                this.IsInInputMode = true
                const dataCode = Object.keys(data_).map(x => data_[x].code)
                const output_  = d.output

                this.config = {
                    IsAddRemove : true,
                    data : Object.keys(output_)
                    .map( x=>  Object.assign(output_[x], { 
                        InWatchList : dataCode.includes(output_[x].code) ? true : false 
                    }))
                }
            } else {
                this.IsInInputMode = false
                this.config = {
                    IsAddRemove : true,
                    data : Object.keys(data_)
                    .map(x => Object.assign(data_[x], { InWatchList : true}))
                }
            }
        },
        add_handleClick(d){
            let args = { code : d.code, hname : d.hname }
            if (d.event === "remove"){
                this.config.data[d.index].InWatchList = false
            } else if (d.event === "add"){
                this.config.data[d.index].InWatchList = true
                if (this.IsInInputMode){
                    args.index=-1
                }else {
                    args.index = this.config.data
                    .slice(0, d.index)
                    .filter(x => x.InWatchList).length
                }
            }
            this.self({
                event : d.event,
                args : args
            })
        },
    }
}