<script>
// vertical and horizontal line drawing tool
// After create new pin object for straight line 

import Overlay from '../../mixins/overlay.js'
import Tool from '../../mixins/tool.js'
import Icons from '../../stuff/icons.json'
import Pin_One from '../primitives/pin_one.js'
import Horizontal from '../primitives/horizontal.js'
import Vertical from '../primitives/vertical.js'

export default {
    name: 'StraightLineTool',
    mixins: [Overlay, Tool],
    methods: {
        meta_info() {
            return { author: 'C451', version: '1.1.0' }
        },
        tool() {
            return {
                // Descriptor for the tool
                group: 'Straight', icon: Icons['horizontal.png'],
                type: 'Horizontal',
                hint: 'This hint will be shown on hover',
                data: [],     // Default data
                settings: {}, // Default settings
                // Modifications
                mods: {
                        'Vertical': {
                        // Rewrites the default setting fields
                        settings: { vertical: true },
                        icon: Icons['vertical.png']
                    }
                }
                
            }
        },
        // Called after overlay mounted
        init() {
        this.pins.push(new Pin_One(this, 'p1'))
    
        },

        draw(ctx) {
            if (!this.p1) return
     
            ctx.lineWidth = this.line_width
            ctx.strokeStyle = this.color
            ctx.beginPath()

            if (this.sett.vertical) {
                new Vertical(this, ctx).draw(this.p1)
            }  else {
                new Horizontal(this, ctx).draw(this.p1)
            }

            ctx.stroke()
            this.render_pins(ctx)
            
        },

        change_color(c) {
            return this.sett.color = c
        },

        change_lineWidth(lw) {
            return this.sett.lineWidth = lw
        },

        change_lineStyle(ls) {
            return this.sett.lineStyle = ls
        },

        use_for() { return ['StraightLineTool'] },
        data_colors() { return [this.color] }
    },

    // Define internal setting & constants here
    computed: {
        sett() {
            return this.$props.settings
        },
        p1() {
            return this.$props.settings.p1
        },
        line_width() {
            return this.sett.lineWidth || 0.9
        },
        color() {
            return this.sett.color || '#42b28a'
        },
        line_style() {
            return this.sett.lineStyle || 'line'
        },
    },
    data() {
        return {}
    }
    

}
</script>
