


export default {
    created() {
        this.comId = {
            'name' : this.$options.name,
            'id' : this._uid
        }
        this.$store.commit("registerComponent",this.comId)
    },
    methods : {
        loadChart (code, lock=false) {
            let chartsList = this.$store.getters.getChartList
            let idx = chartsList.findIndex( x => x.code === code )
            if (idx !== -1){
                if (!lock) {
                    this.$store.commit("updateCodeName", code)
                    // TODO update code to server
                }
                this.$store.commit("updateCharts", idx)
                this.$store.commit("render", {
                    id : this.comId,
                    event : 'load-chart'
                })
                return;
            }
            this.$store.dispatch("getCharts", {
                code : code, 
                id : this.comId,
                lock : !!this.lock
            })
        },
        render () {
                
        },
    },
    watch : {
        '$store.state.render' : function () {
            this.render()
        }
    }
}