<template>
    <div class="body-form-container"
    @click="onClickEvent"
    @mouseover="onMouseUp"
    @mouseleave="onMouseLeave"
    :style="{ 'background-color' : backgroundColor }"
    >
        <div class="list-item-1-1">
            <div class="expandHandleWrap">
            </div>
            <div class="text-item-1-1" 
            :style="{
                color : nameColor,
                'font-size' : `${fontSize}px`,
            }"> {{item.code}} </div>
        </div>
        <div class="list-item-1-2">
            <div class="text-item-1-2"
            :style="{
                color : nameColor,
                'font-size' : `${fontSize}px`,
            }"> {{item.hname}} </div>
        </div>
    </div>
</template>

<script>
export default {
    props : {
        item : {
            type : Object,
            default() {
                return { 
                    code  : "",
                    hname : ""                
                }
            }
        },
        nameColor : {
            type : String,
            default : "#787b86",
        },
        fontSize : {
            type : Number,
            default : 11,
        },
        IsHover : {
            type : Boolean,
            default : false,
        },
        IsAddRemove : {
            type : Boolean,
            default : false,
        },
        index : {
            type : Number,
            default : -1
        }
    },
    computed : {
    },
    methods : {
        onClickEvent(e) {
            e.preventDefault()
            this.$emit('onClickEvent', {
                code  : this.$props.item.code,
                hname : this.$props.item.hname,
                index : this.$props.index,
                event : "selected" }
            )
        },
        onMouseUp(e) {
            if (this.$props.IsHover) {
                this.backgroundColor = "#2a2e39"
            }
        },
        onMouseLeave (e) {
            if (this.$props.IsHover) {
                this.backgroundColor = null
            }
        },
    },
    data () {
        return {
            backgroundColor : null,
            constantBackground : "#1e222d",
        }
    }
}
</script>

<style scoped>
.body-form-container {
    display: flex;
    flex: 1 1 auto;
    width: 97%;
    align-items: center;
}
.expandHandleWrap{
    display: flex;
    align-items: center;
    width: 20px;
    flex-shrink: 0;
}
.list-item-1-1{
    display: flex;
    position: sticky;
    top: 0;
    align-items: center;
    justify-content: space-between;
    height: 40px;
    padding-right: 24px;
}
.text-item-1-1{
    font-weight: 400;
    font-style: normal;
    line-height: 16px;
    letter-spacing: .4px;
    text-transform: uppercase;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width : 70px;
}
.list-item-1-2{
    flex-grow: 1;
    display: flex;
    position: sticky;
    top: 0;
    align-items: center;
    justify-content: space-between;
    height: 40px;
}
.text-item-1-2{
    padding-right: 17px;
    display: flex;
    position: sticky;
    top: 0;
    align-items: center;
    justify-content: space-between;
    height: 40px;
}
.list-button-last{
    align-items: center;
    justify-content: center;
    width : 48px;
    left : -48;
    padding-right: 12px;
    color: #787b86;
}
.svg-padding{
    margin-top : 7px;
}
</style>