<template>
    <div class = "drag-tool-menu-color">    
        <swatches v-model="colors"> </swatches>      
        <div id="specific"></div>
    </div>
</template>

<script>
import { Swatches } from 'vue-color'

export default {
    name: "drag-tool-menu-color",
    components: {
        Swatches
    },

    data () {
        return{
            colors: {
                hex: '#194d33',
                hsl: { h: 150, s: 0.5, l: 0.2, a: 1 },
                hsv: { h: 150, s: 0.66, v: 0.30, a: 1 },
                rgba: { r: 25, g: 77, b: 51, a: 1 },
                a: 1
            }
        }
    },

    methods: {
        changeBack() {
            var el = document.getElementById("specific")
            el.style.backgroundColor = this.colors.hex
            this.$store.commit("updateColor", this.colors.hex)
            this.$store.commit("updateColorState", true)


        }
    },

    watch: {
        'colors' : 'changeBack'
    }

}
</script>
