<template>
<div>
    <div id="top-toolbar-container" :style="topToolBarStyle">
        <top-tool-wrapper 
        :tf="tf"
        :code="code"
        :hname="hname"
        @top-tool-event="top_tool_event"
        >
        </top-tool-wrapper>
    </div>
    <div id="trading-vue-container">
        <trading-vue :data="chart" :width="this.width" :height="this.height"
            :toolbar="true"
            :titleTxt="hname"
            :index-based="index_based"
            :color-back="colors.colorBack"
            :color-grid="colors.colorGrid"
            :color-text="colors.colorText"
            ref="tradingVue">
        </trading-vue>
    </div>
</div>
</template>

<script>
import TradingVue from '../../src/TradingVue.vue'
import TopToolWrapper from './topTool/TopToolWrapper.vue'
import Data from '../data/057540.json'
import comAdapter from './js/comAdapter'
import DataCube from '../../src/helpers/datacube.js'

const TIME_FRAME = ['1Min', '3Min', '5Min', '10Min', '15Min', '30Min', '45Min', 'D']

export default {
    name: "MainChart",
    props: [],
    mixins: [comAdapter],
    components: { 
        TradingVue, TopToolWrapper
    },

    methods: {
        onResize(event) {
            this.width = window.innerWidth * 3 / 4 - 10
            this.height = window.innerHeight * 19 / 23 - 50
         },
        handle_change(d){
            console.log(this.cur_hname)
            if ("charts" in d){
                this.charts = d.charts
                this.chart.set('chart.data', this.charts[this.tf])
            }
        },
        top_tool_event(d){
            switch(d.event){
                case "change-tf": this.change_tf(d.args)
                    break
                case "upload-code" : 
                    this.loadChart(d.args.code)
                    break
            }
            
        },
        change_tf(tf) {
            if (tf === this.tf) {
                return;
            }
            this.tf = tf

            if (!this.lock){
                this.$store.commit("updateTF", tf);
                // TODO update TF to server
            }
            this.chart.set('chart.data', this.charts[tf]) 
            console.log(this.chart)
            this.$refs.tradingVue.resetChart()

        },
        render () {
            let d = this.$store.getters.getRequestInfo
            if ((this.lock) && (d.id !== this._uid)){
                // lock
                return;
            }
            switch(d.event) {
                case "init-page":
                    this.code   = this.$store.getters.getCode
                    this.hname  = this.$store.getters.getHname
                    this.charts = this.$store.getters.getChart.chart

                    this.chart.set('chart.data', this.charts[this.tf])

                    if (this.code){
                        this.$store.commit("updateOnChartGrid", [this.code,this.chart.data.onchart])
                        this.$store.commit("updateOnChartGrid", [this.code,this.chart.data.onchart])
                    }
                    break


                case 'load-chart':
                    var grid_store = this.$store.getters.getOnChartGrid

                    if (grid_store[this.code] && this.code){
                        this.$store.commit("updateOnChartGrid", [this.code,this.chart.data.onchart])
                    }
                    

                    this.code   = this.$store.getters.getCode
                    this.hname  = this.$store.getters.getHname
                    this.charts = this.$store.getters.getChart.chart

                    this.chart.set('chart.data', this.charts[this.tf])

                    if (grid_store[this.code] === undefined){
                        this.$store.commit("updateOnChartGrid", [this.code,this.chart.data.onchart])
                    }

                    if (grid_store[this.code] != undefined && grid_store[this.code]){
                        this.chart.data.onchart = grid_store[this.code]
                    }
                    else{
                        this.$store.commit("updateOnChartGrid", [this.code,this.chart.data.onchart])
                        this.chart.data.onchart = []
                    }

                    if (d.event == "load-chart"){
                        // TODO update code to server
                    }
                    
                    break;

            }
        }
    },
    mounted() {
        // @TODO  user info   - tf, code, hname
        this.tf = this.$store.getters.getTF

        window.addEventListener('resize', this.onResize)
        this.onResize()

        this.chart.set('chart.data', this.charts[this.tf])

        window.dc = this.chart
        window.tv = this.$refs.tradingVue

    },
    
    beforeDestroy() {
        window.removeEventListener('resize', this.onResize)
    },

    computed: {
        colors() {
            return {
                colorBack: 'rgba(30, 32, 38, 0.498)',//'#fff',
                colorGrid: '#333', //'#eee',
                colorText: '#fff',//'#333'
            }
        },
        topToolBarStyle() {
            return {
                background : 'rgba(30, 32, 38, 0.498)',//'#fff',
                height: 37 + 'px',
                width: this.width + 'px',
                //color: '#f57c00'//'#131722'
                color: "#fff"
            }
        },
    },

    data() {
        return {
            charts: Data,
            chart: new DataCube({}),
            width:  window.innerWidth,
            height: window.innerHeight,
            index_based: true,

            tf : "",
            code: "000020",
            hname: "",
            lock : false, 
        };
    },

    watch: {
        
    },
}
</script>

<style>

#top-toolbar-container{
    position: absolute;
    /*height: 50px;
    color: #ddd;
    width: 100vw;*/
    background: #121826; 
    border-bottom: 1px solid #f57c00;/*#8080804f;*/
    font-size: 14px;
    z-index: 102;
    border-radius: 10px 10px 0% 0%;
}
#trading-vue-container{
    position: absolute;
    top: 37px;
    width: 100%;
}
</style>