<template>
    <div class="watch-list-item-alert"
    @mouseover="onMouseUp"
    @mouseleave="onMouseLeave"
    @click="onClickEvent"
    :style="selectedStyle"
    >
        <span class="watch-list-flag"></span>
        <span class="watch-list-name" :style="{
            color : cColor,
            'fontSize' : `${fontSize}` + 'px' 
        }"> 
            <div style="text-align:left;">
            {{item.hname}}{{" - "}}{{item.code}}
            <br>{{parseInt(item.price)}}
            </div>
        </span>

        <div class="watch-list-settingButton" 
        v-if="mouseIsHover"
        style="color : #fff"
        @click="onSetting"
        >
            <span class="settingButton-text">
                <svg class="svg-watch-list-padding" xmlns="http://www.w3.org/2000/svg" width="18" height="18">
                    <path fill="currentColor" d="M5.005 16A1.003 1.003 0 0 1 4 14.992v-1.984A.998.998 0 0 1 5 12h1.252a7.87 7.87 0 0 1 .853-2.06l-.919-.925c-.356-.397-.348-1 .03-1.379l1.42-1.42a1 1 0 0 1 1.416.007l.889.882A7.96 7.96 0 0 1 12 6.253V5c0-.514.46-1 1-1h2c.557 0 1 .44 1 1v1.253a7.96 7.96 0 0 1 2.06.852l.888-.882a1 1 0 0 1 1.416-.006l1.42 1.42a.999.999 0 0 1 .029 1.377s-.4.406-.918.926a7.87 7.87 0 0 1 .853 2.06H23c.557 0 1 .447 1 1.008v1.984A.998.998 0 0 1 23 16h-1.252a7.87 7.87 0 0 1-.853 2.06l.882.888a1 1 0 0 1 .006 1.416l-1.42 1.42a1 1 0 0 1-1.415-.007l-.889-.882a7.96 7.96 0 0 1-2.059.852v1.248c0 .56-.45 1.005-1.008 1.005h-1.984A1.004 1.004 0 0 1 12 22.995v-1.248a7.96 7.96 0 0 1-2.06-.852l-.888.882a1 1 0 0 1-1.416.006l-1.42-1.42a1 1 0 0 1 .007-1.415l.882-.888A7.87 7.87 0 0 1 6.252 16H5.005zm3.378-6.193l-.227.34A6.884 6.884 0 0 0 7.14 12.6l-.082.4H5.005C5.002 13 5 13.664 5 14.992c0 .005.686.008 2.058.008l.082.4c.18.883.52 1.71 1.016 2.453l.227.34-1.45 1.46c-.004.003.466.477 1.41 1.422l1.464-1.458.34.227a6.959 6.959 0 0 0 2.454 1.016l.399.083v2.052c0 .003.664.005 1.992.005.005 0 .008-.686.008-2.057l.399-.083a6.959 6.959 0 0 0 2.454-1.016l.34-.227 1.46 1.45c.003.004.477-.466 1.422-1.41l-1.458-1.464.227-.34A6.884 6.884 0 0 0 20.86 15.4l.082-.4h2.053c.003 0 .005-.664.005-1.992 0-.005-.686-.008-2.058-.008l-.082-.4a6.884 6.884 0 0 0-1.016-2.453l-.227-.34 1.376-1.384.081-.082-1.416-1.416-1.465 1.458-.34-.227a6.959 6.959 0 0 0-2.454-1.016L15 7.057V5c0-.003-.664-.003-1.992 0-.005 0-.008.686-.008 2.057l-.399.083a6.959 6.959 0 0 0-2.454 1.016l-.34.227-1.46-1.45c-.003-.004-.477.466-1.421 1.408l1.457 1.466z">
                    </path>
                </svg>
            </span>
        </div>

        <div class="watch-list-deleteButton" 
        v-if="mouseIsHover"
        style="color : #fff"
        @click="onDeleteItem"
        >
            <span class="deleteButton-text">
                <svg class="svg-watch-list-padding" xmlns="http://www.w3.org/2000/svg" width="18" height="18">
                    <path fill="currentColor" d="M9.707 9l4.647-4.646-.707-.708L9 8.293 4.354 3.646l-.708.708L8.293 9l-4.647 4.646.708.708L9 9.707l4.646 4.647.708-.707L9.707 9z">
                    </path>
                </svg>
            </span>
        </div>

    </div>
</template>

<script>
import { ModalBus } from '../js/eventBus.js'

export default {
    name : "WatchListItemAlert",
    props : {
        item : {
            type : Object,
            default : {
                hname : "",
                code : "000000",
                id : "linetool",
                price : "000000",
            },
        }, 
        selectedCode : {
            type : String,
            default : "",
        },
        HoverColor : {
            type : String,
            default : "#2a2e39"
        },
        color : {
            type : String,
            default : "#fff"
        },
        IsHover : {
            type : Boolean,
            default : false,
        },
        fontSize : {
            type : Number,
            default : 12,
        }
    },
    methods : {
        onMouseUp(e) {
            if (this.$props.IsHover) {
                this.backColor = this.$props.HoverColor
                this.cColor = "rgb(240, 185, 11)"
                this.mouseIsHover = true
            }
        },
        onMouseLeave (e) {
            if (this.$props.IsHover) {
                this.backColor = null
                this.cColor = this.$props.color
                this.mouseIsHover = false
            }
        },
        onClickEvent (e) {
            e.preventDefault()
            if (this.IsDestory) return;
            
            this.$emit("onEvent", {
                event : "selected",
                args : this.$props.item
            })
        },
        onDeleteItem(e) {
            e.preventDefault()
            this.IsDestory = true
            
            const temp = this.$store.getters.getOnChartGrid[this.$store.getters.getCode]

            for (const v in temp) {
                if (temp[v].settings.p1[2]){
                    if (temp[v].settings.$uuid === this.$props.item.id){
                        this.$store.commit("updateOnChartGrid", [this.code, temp.splice(v,1)])
                    
                    }
                }
            }
            
            this.$emit("onEvent", {
                event : "remove",
                args : this.$props.item,
                self : (x) => {
                    console.log("delete "+this.$props.item.id+" component")
                }
            })
        },
        onSetting(e) {
            e.preventDefault()
            this.IsDestory = true
            ModalBus.$emit('open', {
                modal_case : 'modal-setting',
                data : this.item,
                self : (x) => {
                    console.log('open alert setting')
                }
            })
        }
    },
    computed : {
        selectedStyle () {
            return {
                'background-color' : this.backColor,
                border : (this.selectedCode === this.$props.item.code) ? "3px solid #2962ff" : "" 
            }
        }
    },
    data () {
        return {
            cColor : this.$props.color,
            backColor : null,
            mouseIsHover : false,
            IsDestory : false,
        }
    },
}
</script>

<style>
.watch-list-item-alert{
    display: flex;
    flex: 1 1 auto;
    width: 97%;
    border : 1px solid  #787b86;
;
}
.watch-list-flag{
    align-self: center;
    margin-right: 4px;
}
.watch-list-name{
    display: flex;
    min-width: 0;
    max-width: 100%;
    padding-top: 3px;
    padding-bottom: 3px;
    align-items: center;
    overflow: hidden;
    white-space: nowrap;
    justify-content: flex-start;
    padding-right: 3px;
    flex: 1 4 90px;
}
.watch-list-deleteButton{
    align-items: center;
    justify-content: center;
    width : 20px;
    left : -10px;
    padding: 0;
}
.svg-watch-list-padding{
    margin-top : 5px;
}
.svg-watch-list-padding:hover{
    color : #fff;
    background-color: #1e222d;
}
</style>