<template>
    <div class="modal-body-wrap">
        <div class="modal-body-header">
            <component 
            :is="component"
            />
        </div>
        <div class="modal-body-body">
            <perfect-scrollbar ref="modal-scroll">
                <component v-for="(item, index) in config.data"
                :is="component"
                :item="item"
                :key="`${item.code} + ${index}`"
                :index="index"
                :nameColor="nameColor"
                :fontSize="fontSize"
                :IsHover="true"
                :IsAddRemove="config.IsAddRemove"
                @onClickEvent="onClickEvent"
                />
            </perfect-scrollbar>
        </div>
    </div>
</template>

<script>
import { PerfectScrollbar } from "vue2-perfect-scrollbar";
import "vue2-perfect-scrollbar/dist/vue2-perfect-scrollbar.css";

export default {
    name : "ModalBody",
    props : ['component' , 'config'],
    components : { PerfectScrollbar },
    methods : {
        onClickEvent(d) {
            this.$emit('onClickEvent', d)
        }
    },
    computed : {
    },
    data () {
        return {
            groupData : [],
            //IsSearch : false,
            nameColor : '#fff',
            fontSize : 16,
        }
    },
    watch : {
        dispatchReceived () {
            const last_obj = this.$store.getters.get_SIG
            if ("MODAL_BODY" in last_obj) {
                switch(last_obj["MODAL_BODY"]){
                    case "update_cn_dic":
                        const dic_obj = this.$store.getters.getCodeDic
                        const key_arg = Object.keys(dic_obj)
                        this.groupData = Object.values(dic_obj).slice(undefined,100).map(
                            (k, i) => { return { 
                                code : k, hname : key_arg[i], idx : i 
                            }
                        })
                        console.log("SC")
                        break;
                }
                this.$store.commit("deleteSIG")
            }
        },
    }
}
</script>

<style>
.modal-body-wrap{
    position: relative;
    cursor: default;
    overflow:hidden;
    height: 100%;
}
.modal-body-body{
    height: 90%;
}
.ps{
    height : 100%;
}
.ps .ps__rail-y.ps--clicking {
  background-color: transparent;
  opacity: 0.3;
}
.ps .ps__rail-x:hover, .ps .ps__rail-y:hover, .ps .ps__rail-x:focus, .ps .ps__rail-y:focus, .ps .ps__rail-x.ps--clicking, .ps .ps__rail-y.ps--clicking {
    background-color: transparent;
    opacity: 0.9;
}
.ps__rail-y {
    width: 3px;
}
</style>