<template>
  <div container>
    <div class="draggable-tool-bar">
        <drag-tool-bar :content="create_top_tool_content">
        </drag-tool-bar>
    </div>
  </div>
</template>

<script>
import { ModalBus } from './js/eventBus.js';
import DragToolBar from './dragToolbar/DragToolBar.vue'

export default {
    name: "DraggableToolBar",
    props: [],
    components:{
      DragToolBar
    },
    methods: {
    },
    computed: {
        create_top_tool_content() {
          if (this.edit_mode)
            return [
                { text: 'color',
                  menu: [
                  {is : 'color'}
                  ]
                },
                { is: "separator" },
                {                 
                    title: "lineWidth", 
                    html: this.$store.getters.getLineWidthShow ? 
                    '<div class="ellipsis">'+this.$store.getters.getLineWidthShow+'</div>': this.lineWidth,
                    menu: this.linewidth_menu,            
                },
                { is: "separator" },
                {                 
                    title: "lineStyle", 
                    html: this.$store.getters.getLineStyleShow ? 
                    '<div class="ellipsis">'+this.$store.getters.getLineStyleShow+'</div>': this.lineStyle,
                    menu: this.lineStyle_menu,            
                },
                { is: "separator" },
                { 
                  text: this.edit_mode ? "unlock" : "lock",
                 active: !this.edit_mode,
                  click: () => { this.edit_mode = !this.edit_mode } },

            ]
        else
          return [       
                 { text: 'color',
                },
                { is: "separator" },
                {                 
                    title: "lineWidth", 
                    html: this.$store.getters.getLineWidthShow ? 
                    '<div class="ellipsis">'+this.$store.getters.getLineWidthShow+'</div>': this.lineWidth,      
                },
                { is: "separator" },
                {                 
                    title: "lineStyle", 
                    html: this.$store.getters.getLineStyleShow ? 
                    '<div class="ellipsis">'+this.$store.getters.getLineStyleShow+'</div>': this.lineStyle,      
                },
                { is: "separator" },
                { 
                  text: this.edit_mode ? "unlock" : "lock",
                 active: !this.edit_mode,
                  click: () => { this.edit_mode = !this.edit_mode } },

            ]
        },

        linewidth_menu () {
          return this.width_list.map(lineWidth => {
            return {
              html: '<span class="ellipsis" style="font-family:'+lineWidth+'">'+lineWidth+'</span>',
              active: (this.lineWidth == lineWidth),
              text: lineWidth,
              click: () => {
                this.lineWidth = lineWidth;
                this.$store.commit("updateLineWidth", this.lineWidth)
                this.$store.commit("updateLineWidthState", true)
              }
            };
          });
        },

        lineStyle_menu () {
          return this.linestyle_list.map(lineStyle => {
            return {
              html: '<span class="ellipsis" style="font-family:'+lineStyle+'">'+lineStyle+'</span>',
              active: (this.lineStyle == lineStyle),
              text: lineStyle,
              click: () => {
                this.lineStyle = lineStyle;
                this.$store.commit("updateLineStyle", this.lineStyle)
                this.$store.commit("updateLineStyleState", true)
              }
            };
          });
        },

      width_list: () => ["1","2","3","4","5"],
      linestyle_list: () => ["line", "dash line", "dotted line"],
    },

    mounted() {
      this.show = this.$store.getters.getDtoolbar
    },
    beforeDestroy() {},
    data() {
        return {
          show: false,
          edit_mode: true,
          color: "#42b28a",
          lineWidth: "1",
          lineStyle: "line",
        };
    },

    watch: {

    }

}
</script>

<style>
.container {
  color: white;
  font-size: 7px;
  padding: 10px;
  background: rgb(74, 173, 228);
}
</style>