<template>
    <div>
        <modal-setting
        v-if="m1_modal"
        :isOpen="m1_modal" 
        :title="title"
        @onClose="handleModalClose">
            <modal-body-alert slot="modal-body-slot"
            :config="config"
            />
        </modal-setting>

        <modal-setting-list
        v-if="m2_modal"
        :isOpen="m2_modal" 
        :title="title"
        @onClose="handleModalClose">
            <modal-input
                @onHandleInput="onHandleInput"
                slot="modal-input-slot"
            />
            <modal-body-alert-list slot="modal-body-slot2"
            :component="component"
            :config="config"
            />
        </modal-setting-list>

    </div>
</template>

<script>
import { ModalBus } from '../js/eventBus.js'
import ModalBodyAlert from './component/ModalBodyAlert.vue'
import ModalBodyAlertList from './component/ModalBodyAlertList.vue'
import ModalSetting from './ModalSetting.vue'
import ModalSettingList from './ModalSettingList.vue'
import WatchListItemAlert from '../iterableForm/WatchListItemAlert.vue'

import ModalInput from './component/ModalInput.vue'
import SimpleSearchList from './component/SimpleSearchList.vue'

export default {
    components: { ModalSetting, ModalSettingList, SimpleSearchList,
    ModalBodyAlert, WatchListItemAlert, ModalBodyAlertList, ModalInput },
    data () {
        return {
            m1_modal : false,
            m2_modal : false,
            title : '',
            component : null,
            closeOnClick : true, //  
            self : null,
            handleInput : this.handleModalSreachInput,
            config : {
                 default : { 
                    data : [],
                    IsAddRemove : false,
                }
            },
        }
    },
    created () {
        ModalBus.$on('open', ({ modal_case, closeOnClick=true, self=null, data=[]}) => {
            switch (modal_case){
                case "modal-setting":
                    this.m1_modal = true
                    this.title = ""
                    this.config.IsAddRemove = false
                    this.handleInput = this.handleModalSreachInput
                    break;     
            }
            this.closeOnClick = closeOnClick
            this.self = self
        })
        ModalBus.$on('open-list', ({ modal_case, closeOnClick=true, self=null, data=[]}) => {
            switch (modal_case){   
                case "modal-setting-list":
                    this.m2_modal = true
                    this.title = " "
                    this.config.IsAddRemove = false
                    this.handleInput = this.handleModalSreachInput
                    break; 
            }
            this.closeOnClick = closeOnClick
            this.component = SimpleSearchList
            this.self = self
        })
        document.addEventListener('keyup', this.handleKeyup)
    },
    beforeDestroy() {
        document.removeEventListener('keyup', this.handleKeyup)
        this.m1_modal = false
        this.m2_modal = false
        this.handleInput = null
        this.component = null
        this.self = null
    },
    methods: {
        handleModalClose( force=false ) {
            if(!this.closeOnClick && !force) {
                return
            }
            this.m1_modal = false
            this.m2_modal = false
        },
        handleModalSreachInput(data) {
            console.log(data)
        },
        handleModalAddInput(data){
            console.log("ADD", data)
        },
        updateInput(e){
            console.log(e.target.value)
        },
        handleKeyup (e) {
            if (e.keyCode == 27) this.handleClose()
        },
    }
}
</script>

