<template>
<div class="main-form-container"
:style="{
    'background-color' : backgroundColor,
    color : textColor,
}"
>
    <div class="tool-bar-wrapper">
        <top-tool-bar
        v-if="IsTools"
        :content="tools"
        />
    </div>
    <div class="sub-header-wrapper" v-if="IsSubHeaderSlot" >
        <slot name="sub-header-slot"/>
    </div>
    <div class="body-content-wrapper">
        <slot name="body-content-slot"/>
    </div>
    <div class="footer-wrapper" v-if="IsFooterSlot">
        <slot name="footer-slot"/>
    </div>
</div>    
</template>

<script>
import TopToolBar from './topTool/TopToolBar.vue'
export default {
    name : "MainForm",
    components: { TopToolBar },
    props : {
        tools : {
            type : Array,
            default : [],
        },
        backgroundColor : {
            type : String,
            default : "rgba(30, 32, 38, 0.498)"
        },
        textColor : {
            type : String,
            default :  "#b2b5be"
        },
        labelColor : {
            type : String,
            default : "#787b86"
        },
    },
    computed : {
        IsTools () {
            return this.$props.tools.length !== 0 
        },
        IsSubHeaderSlot() {
            return !!this.$slots.subHeaderSlot
        },
        IsFooterSlot() {
            return !!this.$slots.footerSlot
        }
    },
    methods : {

    }
}
</script>

<style>
.main-form-container{
    position: relative;
    display: flex;
    flex-direction: column;
    height: 100%;
    background-color : rgba(43, 47, 54, 0.8);
}
.tool-bar-wrapper{
    /* width: 100%; */
    font-size: 14px;
    margin: 0;
    padding: 10px 16px 4px;
    border-bottom : 1px solid #d1d4dc;
}
.sub-header-wrapper{
    flex: 0, 0, auto;
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0 8px;
    cursor: default;
    height: 39px;
    border-bottom-width: 1px;
    border-bottom-style: solid;
}
.body-content-wrapper {
    position: relative;
    cursor: default;
    overflow: hidden;
    height: 100%;
    padding : 10px 16px 10px;
}
</style>