<template>
    <div>
        <modal-search 
        v-if="m1_modal"
        :isOpen="m1_modal" 
        :title="title"
        @onClose="handleModalClose">
            <modal-input
                @onHandleInput="onHandleInput"
                slot="modal-input-slot"
            />
            <modal-body slot="modal-body-slot"
            :component="component"
            :config="config"
            @onClickEvent="onClickEvent"
            ref="modal-body"
            />
        </modal-search>
        <small-modal
        v-if="m2_modal"
        :isOpen="m2_modal"
        :title="title"
        :deleteItemName="deleteItemName"
        @onClose="handleModalClose">
        </small-modal>
    </div>
</template>

<script>
import { ModalBus } from '../js/eventBus.js'
import ModalInput from './component/ModalInput.vue'
import ModalBody from './component/ModalBody.vue'
import SimpleSearchList from './component/SimpleSearchList.vue'

import ModalSearch from './ModalSearch.vue'
import SmallModal from './SmallModal.vue'

import SearchAddModal from './mixins/SearchAddModal.js'

export default {
    components: { ModalSearch, ModalInput, ModalBody, SimpleSearchList, SmallModal },
    mixins : [SearchAddModal],
    
    data () {
        return {
            m1_modal : false,
            title : '',
            component : null,
            closeOnClick : true, //  
            self : null,
            updateFunc : null,
            onHandleInput : this.search_handleInput,
            onClickEvent : this.search_handleClick,
            IsInInputMode : false,
            config : {
                 default : { 
                    data : [],
                    IsAddRemove : false,
                }
            },
            m2_modal : false,
            deleteItemName : ""
        }
    },
    created () {
        ModalBus.$on('search-add-modal', ({ modal_case, closeOnClick=true, self=null, data=[]}) => {
            switch (modal_case){
                case "modal-search":
                    this.m1_modal = true
                    this.title = " "
                    this.onHandleInput = this.search_handleInput
                    this.onClickEvent = this.search_handleClick

                    this.$set(this.config, 'IsAddRemove', false)
                    this.$set(this.config, 'data', [])

                    this.self = self
                    break;
                case "modal-add":
                    this.m1_modal = true
                    this.title = " "
                    this.onHandleInput = this.add_handleInput
                    this.onClickEvent = this.add_handleClick
                    
                    this.$set(this.config, 'IsAddRemove', true)
                    this.$set(this.config, 'data', Object.keys(data).map(
                        x => Object.assign(data[x], { InWatchList : true})
                    ))

                    this.self = self['onEvent']
                    this.updateFunc = self['getData']
                    break;
            }
            this.component = SimpleSearchList
            this.closeOnClick = closeOnClick
        })
        ModalBus.$on('small-modal', ({ modal_case, title, deleteItemName, closeOnClick=true, self=null}) => {
            switch (modal_case){
                case "delete-item":
                    this.m2_modal = true
                    this.title = title
                    this.deleteItemName = deleteItemName
                    this.component = SmallModal

                    this.self = self
                    break;
            }
            this.closeOnClick = closeOnClick
        })
        document.addEventListener('keyup', this.handleKeyup)
    },
    beforeDestroy() {
        document.removeEventListener('keyup', this.handleKeyup)
        this.m1_modal = false
        this.m2_modal = false
        this.handleInput = null
        this.component = null
        this.self = null
    },
    methods: {
        handleModalClose(okay=false) {
            if (this.m2_modal && okay){
                this.self({ event : 'remove' })
            }
            this.m1_modal = false
            this.m2_modal = false
        },
        handleKeyup(e) {
            if (e.keyCode == 27) this.handleModalClose()
            if ((this.m2_modal) && (e.keyCode == 13)) this.handleModalClose(true)
        },
    }
}
</script>

