<template>
    <div class=group-code-container
    @mouseover="onMouseUp"
    @mouseleave="onMouseLeave"
    :style="{ 'background-color' : backgroundColor }"
    >
        <div class="list-item-1-1">
            <div class="expandHandleWrap">
            </div>
            <div class="text-item-1-1"> {{item.code}} </div>
        </div>
        <div class="list-item-1-2">
            <div class="text-item-1-2"> {{item.hname}} </div>
        </div>
        <div class="list-button-last">
            <svg class="svg-padding" v-if="InGroup"
            @click="onClickEvent"
            xmlns="http://www.w3.org/2000/svg" viewBox="0 0 28 28" width="28" height="28" 
            fill=null :style="{ 'background-color' : constantBackground }"
            >
                <path stroke="currentColor" stroke-width="1.2" d="M8 8l13 13m0-13L8 21">
                </path>
            </svg>
            <svg class="svg-padding" v-else
            @click="onClickEvent"
            xmlns="http://www.w3.org/2000/svg" viewBox="0 0 28 28" width="28" height="28" 
            fill="none" :style="{ 
                'background-color' : '#1e222d',
                 color : '#2962ff'
            }">
                <path fill="currentColor" fill-rule="evenodd" clip-rule="evenodd" d="M13.9 14.1V22h1.2v-7.9H23v-1.2h-7.9V5h-1.2v7.9H6v1.2h7.9z">
                </path>
            </svg>
        </div>
    </div>
</template>

<script>
export default {
    props : ['item', 'InGroup'],
    methods : {
        onClickEvent(e) {
            e.preventDefault()
            this.$emit('onClickEvent', {
                event : this.$props.InGroup ? 'remove-in-codelist' : 'add-in-codelist',
                code : this.$props.item.code,
                hname : this.$props.item.hname,
            })
        },
        onMouseUp(e) {
            this.backgroundColor = "#2a2e39"
        },
        onMouseLeave (e) {
            this.backgroundColor = null
        },
    },
    data () {
        return {
            backgroundColor : null,
            constantBackground : "#1e222d",
        }
    }
}
</script>

<style scoped>
.group-code-container{
    display: flex;
    flex: 1 1 auto;
    width: 100%;
    align-items: center;
    color: #d1d4dc;
    font-size: 15px;
}
.expandHandleWrap{
    display: flex;
    align-items: center;
    width: 20px;
    flex-shrink: 0;
}
.list-item-1-1{
    display: flex;
    position: sticky;
    top: 0;
    align-items: center;
    justify-content: space-between;
    height: 40px;
}
.text-item-1-1{
    font-weight: 400;
    font-style: normal;
    line-height: 16px;
    text-transform: uppercase;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width : 70px;
}
.list-item-1-2{
    flex-grow: 1;
    display: flex;
    position: sticky;
    top: 0;
    align-items: center;
    justify-content: space-between;
    height: 40px;
}
.text-item-1-2{
    display: flex;
    position: sticky;
    top: 0;
    align-items: center;
    justify-content: space-between;
    height: 40px;
}
.list-button-last{
    align-items: center;
    justify-content: center;
    width : 48px;
    left : -48;
    padding-right: 12px;
    color: #787b86;
}
.svg-padding{
    margin-top : 7px;
}
</style>