
export default {
    created () {
        this.reader = new FileReader()
    },
    methods : {
        parseCSVFile(file){
            let lines = file.split('\r\n')
            if (lines.length < 2) { return null; }
            const numCols = lines[0].split(',').length
            
            return lines
            .map( x => x.split(','))
            .filter( (x, i) => (x.length >= numCols) && (i !== 0))
            .map(x => x.pop())
            .map(x => {
                if (x.length === 6) return x
                if (x.length > 6) return x.slice(x.length - 6)
                return x.padStart(6,"0")                
            })
        },
        fileSaver(obj, filename, contentType){
            const a = document.createElement('a')
            const file = new Blob([obj], {type:contentType})

            a.href= URL.createObjectURL(file);
            a.download = filename;
            a.click();
            
            URL.revokeObjectURL(a.href);
        }
    }
}